window.addEventListener('DOMContentLoaded', () => {
    $('input[type="tel"]').mask('+7 999 999-9999', { autoclear: false }, { placeholder: '+7             ' });
    $(".fg select").niceSelect()

    fix100vh();
    window.addEventListener('resize', () => {
        fix100vh();
        // getScrollBarSize()
    })

    // getScrollBarSize()


})

function fix100vh() {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
}

$(".submenu__back").click(function () {
    $(this).parents(".submenu").removeClass("_show-child");
    $(".submenu2").removeClass("_active");
})

$("._open-submenu").click(function () {
    let id = $(this).attr("data-id");
    $(this).parents(".submenu").addClass("_show-child");
    $(`.submenu2[data-id="${id}"]`).addClass("_active");
    $(".menu").addClass("_open-level3");
})

$(".menu-item").mouseover(function () {
    let id = $(this).attr("data-id");
    openMenuItemDesktop(id)
})

$("._open-category").click(function () {
    let id = $(this).attr("data-id");
    if(window.innerWidth>576){
        openMenuItemDesktop(id)
    } else{
        openMenuItemMob($(`.menu-item[data-id="${id}"]`))
    }
})

$(".menu-item").click(function () {
    if(window.innerWidth>576){
        return
    }
    openMenuItemMob(this)
})

function openMenuItemDesktop(id){
    $(".submenu").removeClass("_show-child");
    $(".submenu").removeClass("_active");
    $(".submenu2").removeClass("_active");
    $(`.submenu[data-id="${id}"]`).addClass("_active");
    $(".menu-item").removeClass("_blue");
    $(`.menu-item[data-id="${id}"]`).addClass("_blue");
}

function openMenuItemMob(menuItem){
    let id = $(menuItem).attr("data-id");
    let title = $(menuItem).find(".menu-item__title").html();
    $(".menu").addClass("_open-level2");
    $(".submenu").removeClass("_active-mob");
    $(`.submenu[data-id="${id}"]`).addClass("_active-mob");
    $(".menu-top__title2").html(title)
}

$(".menu-top__back").click(function () {
    if($(".menu").hasClass("_open-level3")){
        $(".menu").removeClass("_open-level3");
        $(".submenu2").removeClass("_active");
        $(".submenu").removeClass("_show-child");
    } else{
        $(".menu").removeClass("_open-level2");
        $(".submenu").removeClass("_active-mob");
    }
})

$(".menu-top__close").click(function () {
    $(".menu-item").removeClass("_open-menu2")
    $(".menu-item2").removeClass("_open-menu3")
    $(".header").removeClass("_open-catalog-menu")
    $("body").removeClass("fixed-body")

    $(".menu").removeClass("_open-level2");
    $(".submenu").removeClass("_active-mob");
    $(".menu").removeClass("_open-level3");
    $(".submenu2").removeClass("_active");
    $(".submenu").removeClass("_show-child");
})